.manageLayout_container {
  margin: 0 auto;
  width: 80%;
  display: flex;
  min-height: calc(100vh - 64px - 71px);
  .nav {
    width: 20%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 10px;
    ul {
      li {
        text-align: center;
        margin: 10px;
        padding: 10px;
        border-radius: 10px;
        cursor: pointer;
        &:hover {
          background-color: #eee;
        }
      }
      .selected {
        background-color: white;
      }
    }
    .nav_text {
      margin: 0 10px;
    }
  }
  .content {
    width: 80%;
    height: 100%;
    align-items: center;
    .content_header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .input_box {
        width: 40%;
      }
      .searchIcon {
        cursor: pointer;
      }
    }
  }
}
