.loginBox {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.loginTitle {
  font-size: 30px;
  padding-bottom: 25px;
  text-align: center;
}
#basic {
  background-color: #fff;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  width: 50%;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
