.home-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: calc(100vh - 64px - 71px);
  width: 100%;
  background-image: linear-gradient(
    45deg,
    #ff9a9e 0%,
    #fad0c4 99%,
    #fad0c4 100%
  );
  .info {
    text-align: center;
    button {
      height: 60px;
      font-size: 24px;
    }
  }
}
